<template>
    <div class="education-page">
        <div class="container">
            <div class="content-block">
                <div class="education-title">
                    <h1>{{info.data.education_title[0].text}}</h1>
                </div>
                <div class="education-video-wrap">
                    <iframe :src="`https://youtube.com/embed/${info.data.installation_video.embed_url.split('=')[1]}`"></iframe>
                </div>
                <div class="education-video-text">
                    <p v-for="line in info.data.video_text">{{line.text}}</p>
                </div>
                <div class="education-details">
                    <template v-for="line in info.data.installation_directions">
                        <h2 v-if="line.type == 'heading4'">{{line.text}}</h2>
                        <p v-if="line.type == 'paragraph'">{{line.text}}</p>
                        <ul v-if="line.type == 'list-item'">
                            <li>{{line.text}}</li>
                        </ul>
                        <ol v-if="line.type == 'o-list-item'">
                            <li>{{line.text}}</li>
                        </ol>
                        <img v-if="line.type == 'image'" :src="`${line.url}`" :alt="`${line.alt}`">
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import Patterns from './patterns/AllPatterns'
    import ColorFilter from './filters/ColorFilter'
    import PatternFilter from './filters/PatternFilter'
    import AppliedFilters from './filters/AppliedFilters'
    import FilteredPatterns from './patterns/FilteredPatterns'

    export default {
        components: {
            Patterns, ColorFilter, PatternFilter, AppliedFilters, FilteredPatterns
        },
        name: 'HelloWorld',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        computed: {
            ...mapGetters({
                info: 'getEducationFromStore'
            })
        },
        methods: {
            ...mapActions({
                getEducation: 'getEducation'
            })
        },
        mounted() {
            this.getEducation();
            window.scrollTo(0, 0);
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h1, h2 {
        font-weight: normal;
    }

    ul {
        list-style-type: disc;
        padding: 0;
    }

    li {
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }
</style>



