<template>
    <div id="room-picker">
   <h3>ROOM PICKER : {{ pdpRoom.image }}</h3>
        <!-- TODO : make this come from Algolia? fakeAPI? whhhheeerrrreee???? -->
        <span @click="setRoomHere('livingRoom')" class="plain button" :class="{selected: pdpRoom.name == 'livingRoom'}">living room</span>
        <span @click="setRoomHere('bedroom')" class="plain button" :class="{selected: pdpRoom.name == 'bedroom'}">bedroom</span>
        <span @click="setRoomHere('kitchen')" class="plain button" :class="{selected: pdpRoom.name == 'kitchen'}">kitchen</span>
        <span @click="setRoomHere('nursery')" class="plain button" :class="{selected: pdpRoom.name == 'nursery'}">nursery</span>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'

    export default {
        /*    components : {
              WallpaperPattern
            },*/
        computed: {
            ...mapGetters({
                pdpRoom: 'getPdpRoomFromStore',
            })
        },
        methods: {
            ...mapActions({
                setRoomHere: 'setRoom',
            })
        }
    }
</script>



