<template>
    <div class="color-picker">
      <div class="color-option plain button" v-for="color in colors" :class="{ selected : colorSelected == color }" @click="selectColor(color)" :style="{'background-color': color}"></div>
    </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
    computed: {
      ...mapGetters({
            colors: 'getColorsAvailableFromStore',
            colorSelected: 'getColorSelectedFromStore'
        })
    },
    methods: {
        ...mapActions({
            selectColor: 'selectColor',
            findPdpProduct: 'findPdpProduct',
            colorExists: 'colorExists'
        })
    },
    updated() {
      this.findPdpProduct();
        console.log('this.colors',this.colors)
        this.selectColor()
    }
  }
</script>



