<template>
  <div class="cart-total">
    <div class="big checkout button" @click="showModal">CHECKOUT</div>
    <div class="content-block">
      <h1>CART TOTAL</h1>
      <div class="cart-line">
        <span>Subtotal</span>
        <span>${{cartTotal}}</span>
      </div>
      <hr>
      <div class="cart-line">
        <span>Shipping</span>
        <span>${{calcShipping()}}</span>
      </div>
      <div class="cart-line">
        <span>Tax Estimate</span>
        <span>${{calcTax()}}</span>
      </div>
      <hr>
      <div class="cart-line">
        <span>Total</span>
        <span>${{calcFinalTotal()}}</span>
      </div>
    </div>
    <div class="big checkout button">CHECKOUT</div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {

  computed: {
    ...mapGetters({
        //TODO Get shipping, tax, and subtotal
        cartTotal: 'getCartTotalFromStore',
        salesTaxPercent: 'getSalesTaxPercentFromStore',
        shippingCost: 'getShippingCostFromStore'
    })
  },
  methods: {
    calcTax() {
      var tax = this.cartTotal * (this.salesTaxPercent / 100)
      var rounded = Math.round(tax * 100) / 100
      return rounded
    },
    calcShipping() {
      var shipping = Math.round(this.cartTotal * this.shippingCost * 100) / 100
      return shipping
    },
    calcFinalTotal() {
      var total = this.calcTax() + this.calcShipping() + this.cartTotal
      return Math.round(total * 100) / 100
    },
    ...mapActions({
        //TODO
        getCartTotal: 'getCartTotal',
        getShipping: 'getShipping'
    })
  },
  mounted () {
    this.getCartTotal()
  }
}
</script>

<style scoped lang="scss">
  img{
    width: 50px;
    height: 50px;
  }
</style>



