<template>
    <div class="quantity-selector">
      <span class="minus-one button" @click="changeQuantity(-1)">-</span><span class="quantity-selected">{{quantitySelected}}</span><span class="plus-one button" @click="changeQuantity(1)">+</span>
    </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
    computed: {
      ...mapGetters({
            quantitySelected: 'getQuantitySelectedFromStore'
        })
    },
    methods: {
        ...mapActions({
            changeQuantity: 'changeQuantity'
        })
    }
  }
</script>



