<template>
  <div class="patterns">
    <!-- TODO : when you hit a filter it does an api call-->
   <!-- <div v-for="pattern in patterns">
    {{wallpaperFilter(pattern)}}
    </div>-->

    <wallpaper-pattern v-for="pattern in algoliaResults" :key="pattern.sku" v-bind:data="pattern"></wallpaper-pattern>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import WallpaperPattern from './WallpaperPattern'

  export default {
    components: {
      WallpaperPattern
    },
    computed: {
      ...mapGetters({
        patterns: 'getPatternsFromStore',
        filters: 'getFiltersFromStore',
        algoliaResults: 'getAlgoliaResultsFromStore'
      })
    },
    methods: {
      ...mapActions({
        wallpaperFilter: 'wallpaperFilter'
      })
    }
  }
</script>





