<template>
  <div class="pdp">
    <div class="container">
      <div class="content-block">
        <calculator-modal></calculator-modal>

        <div class="pdp-wrap">
          <div class="pdp-inner">
            <div class="image-wrap">
              <room-view v-if="selectedProduct" v-bind:pattern="selectedProduct.image"></room-view>
              <room-picker></room-picker>
            </div> 
            <div class="product-detail-wrap">
              <h1 class="no-margin-t">{{patternInfo.name}}</h1>
              <material-picker></material-picker>
              <color-picker></color-picker>
              <div class="button" @click="showModal">Calculator</div>
              <quantity-selector></quantity-selector>
              <div class="price" v-if="selectedProduct">${{selectedProduct.price}}</div>
              <add-to-cart v-bind:pattern="selectedProduct">
              </add-to-cart>
              <h3>{{patternInfo.products[0].design.type}}</h3>
              {{patternInfo.products[0].design.description.long}}
              
              <social-sharing url="https://vuejs.org/"
                              title="The Progressive JavaScript Framework"
                              description="Intuitive, Fast and Composable MVVM for building interactive interfaces."
                              quote="Vue is a progressive framework for building user interfaces."
                              hashtags="vuejs,javascript,framework"
                              twitter-user="vuejs"
                              inline-template>
                <div>


                  <network network="facebook">
                    <span class="social-media-icon icon-facebook"></span>
                  </network>
                  <network network="pinterest">
                    <span class="social-media-icon icon-pinterest"></span>
                  </network>
       <!--           <network network="reddit">
                    <i class="fa fa-reddit"></i> Reddit
                  </network>-->
                  <network network="twitter">
                    <span class="social-media-icon icon-twitter"></span>
                  </network>

                </div>
              </social-sharing>
            </div> 
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import VueGoodshare from 'vue-goodshare'
    import SocialSharing from 'vue-social-sharing'
    import AddToCart from '@/components/cart/AddToCart'
    import MaterialPicker from '@/components/pdp/MaterialPicker'
    import ColorPicker from '@/components/pdp/ColorPicker'
    import RoomPicker from '@/components/pdp/RoomPicker'
    import RoomView from '@/components/pdp/RoomView'
    import QuantitySelector from '@/components/pdp/QuantitySelector'
    import CalculatorModal from '@/components/modals/CalculatorModal'


export default {
  name: 'PDP',
    components : {
        VueGoodshare, SocialSharing, AddToCart, MaterialPicker, ColorPicker, RoomView, RoomPicker, QuantitySelector, CalculatorModal
    },
  data () {
    return {
      materialSelected: '',
      price: null
    }
  },
    computed: {
        ...mapGetters({
            //TODO : make Pattern Info Grabber
            patternInfo: 'getPatternInfoFromStore',
            selectedProduct: 'getSelectedProductFromStore',
            quantity: 'getQuantitySelectorFromStore'
        })
    },

    methods: {
        showModal() {
            this.$modal.show('calculator')
        },
        hideModal() {
            this.$modal.hide('calculator')
        },
        ...mapActions({
            getPatternInfo: 'getPatternInfo',
            clearPdp: 'clearPdp'            
        })
    },
    created() {
        //this.clearPdp()
        this.getPatternInfo(this.$route.params.pattern)
        window.scrollTo( 0, 0 )
    }
/*  components : {
    TODO: Add components
  }*/
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#room-view{
  height: 500px;
}
</style>



