<template>
    <div class="cart-items">
        <div v-if="!cart.length">
            <div class="title">Your Cart Is Empty :(</div>
            <router-link class="big button" to="/filter-search">Start Shopping!</router-link>
        </div>
        <div v-for="item in cart" class="cart-item">
                <router-link class="cart-image-wrap" :to="`/pdp/${item.design.name}`"><img :src="`${item.image}`" :alt="`${item.design.name}`" class="cart-image"></router-link>
                <router-link class="no-style" :to="`/pdp/${item.design.name}`"><div class="pattern-name title">{{item.design.name}} - {{item.material}}</div></router-link>
                <span class="cart-item-price">${{Math.round(item.price*item.quantity*100)/100}}<br>({{item.quantity}}&nbsp;@&nbsp;{{item.price}})</span>
                <div class="subtext">{{item.design.description.short}}</div>
                <div class="button-wrap">
                    <cart-quantity-selector :product="item"></cart-quantity-selector>
                    <div class="tiny plain red button" @click="removeFromCart(item)">Remove</div>
                </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import WallpaperPattern from '@/components/patterns/WallpaperPattern'
    import CartQuantitySelector from '@/components/cart/CartQuantitySelector'

    export default {
        components: {
            WallpaperPattern, CartQuantitySelector
        },
        computed: {
            ...mapGetters({
                cart: 'getCartFromStore'
            })
        },
        methods: {
            ...mapActions({
                getCartHere: 'getCart',
                removeFromCart: 'removeFromCart',
                getCartTotal: 'getCartTotal',
                getSalesTax: 'getSalesTax',
                getShippingCost: 'getShippingCost'
            })
        },
        mounted () {
            this.getCartHere()
        },
        updated() {
            this.getCartTotal()
            this.getSalesTax()
            this.getShippingCost()
        }
    }
</script>




