<template>
  <div class="artists-page">
    <div class="artists-container">
      <div class="artist-list">
        <div v-for="artist in artists" class="artist-content-block">
          <router-link :to="`/artists/${artist.uid}`"><img :src="`${artist.data.featured_work.url}`"></router-link>
          <router-link :to="`/artists/${artist.uid}`">
            <div>{{artist.data.artist_name[0].text}}</div>
            <span class="subtext">{{artist.data.occupation}}</span>
          </router-link>
          <p>{{artist.artist_bio}}</p>
        </div>
      </div>
    </div>
    <artist-info></artist-info> <!-- Option A -->
    <!-- <marketing-panel v-bind:page="'artists'"></marketing-panel> -->  <!-- Option B -->
  </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import Patterns from './patterns/AllPatterns'
    import ColorFilter from './filters/ColorFilter'
    import PatternFilter from './filters/PatternFilter'
    import AppliedFilters from './filters/AppliedFilters'
    import FilteredPatterns from './patterns/FilteredPatterns'
    import ArtistInfo from './artists/ArtistInfo'
    import MarketingPanel from './marketing/MarketingPanel'

    export default {
        components: {
            Patterns, ColorFilter, PatternFilter, AppliedFilters, FilteredPatterns, ArtistInfo, MarketingPanel
        },
        name: 'HelloWorld',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        computed: {
            ...mapGetters({
                artists: 'getArtistsFromStore'
            })
        },
        methods: {
            ...mapActions({
                getArtistsHere: 'getArtists'
            })
        },
        mounted : function (){
            this.getArtistsHere();
            window.scrollTo( 0,0 );
        }
      /*  components : {
       TODO: Add components
       }*/
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
</style>


