<template>
  <modal name="calculator" adaptive=true width="650px" class="calculator modal">
      <div class="modal-wrap">
          <div class="modal-half">
            <div class="modal-inner">
              <div @click="hideModal" class="modal-close">&times;</div>
              <div class="title">Wallpaper Calculator</div>
              <div class="subtext">All units in Feet</div>
              <div class="modal-input-group">
                  <div class="modal-title">Wall Size:</div>
                  <input type="number" name="wall-size-width" class="modal-number-input" placeholder="W" v-model="width" :class="{'red-border': showError}"> 
                  &times; 
                  <input type="number" name="wall-size-height" class="modal-number-input" placeholder="H" v-model="height" :class="{'red-border': showError}">
              </div>
              <div class="button" @click="calculate">Calculate</div>
              <div class="total" v-if="total">You need {{total}} sq.ft. of wallpaper!</div>
              <div class="error" v-if="showError">Please enter a width and height</div>
            </div>
          </div>
          <div class="modal-half modal-image">
          </div>
      </div>    
  </modal>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  data() {
    return {
      width: null,
      height: null,
      total: null,
      showError: false
    }
  },
  methods: {
    showModal() {
      this.$modal.show('calculator')
    },
    hideModal() {
      this.$modal.hide('calculator')
    },
    calculate() {
      this.showError = false
      this.total = this.width*this.height;
      if (!this.total) {
        this.showError = true
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



