                background-position: center;
                position: relative;
                .side-big-copy{
                    font-family: $header-font;
                    background-color: $footer-cta-color;
                    color: $footer-cta-background-color;
                    font-size: 1.5em;
                    padding: 20px;
                    border: $homepage-border;
                    text-decoration: none;
                }
                @include under-m {
                    height: 50%;
                    margin-bottom: 10px;
                }
                .button{
                    position: relative;
                    z-index: 1;
                }
                .vid {
                    position: absolute;
                    top: 0; left:0;
                    width: 100%; height: 100%;
                    z-index: 0;
                    background-color: $footer-cta-background-color;
                }
                video{
                    object-fit: fill;
                   /* position: absolute;
                    z-index: 0;
                    min-width: 100%;
                    top: 0;
                    left: 0;
                    overflow: hidden;*/
                }
            }
        }
    }

    /*
END HOME
*/

    /*
 START WALLPAPER PATTERN
 */
    ul.pagination {
        width: 100%;
        text-align: center;
        margin-bottom: 30px;
        padding: 0;
        .ais-pagination__item {
            margin: 0 5px;
            background-color: #fff;
            padding: 5px 10px;
            border: 1px solid $highlight-color;
            .ais-pagination__link {
                text-decoration: none;
                color: $highlight-color;
                font-family: $content-font;
                font-size: 1.25em;                
            }
        }
        li {
            display: inline-block;
            padding: 0 5px;
        }
    }
    .result__info {
        text-align: center;
        background-color: white;
        padding: 10px 0;
        border: $parent-border;
        border-top: 0px;
        .result__type {
            display: none;
        }
        .result__price {
            margin: 5px 0;
            font-size: 1em;
        }
    }
    .wallpaper-pattern {
        word-wrap: break-word;
        border: 1px solid $content-accent-color;
        background-color: white;
        color: $content-text-color;
        display: flex;
        justify-content: center;
        position: relative;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        a {
            text-decoration: none;
            color: $highlight-color;
        }
        h1, h2, h3, h4, h5 {
            padding: 0;
            margin: 0;
            text-align: center;
        }
        //TODO: Remove?
        // .color-corner-wrap {
        //     position: absolute;
        //     top: 0;
        //     left: 0;
        //     width:100px;
        //     height:100px;
        //     overflow:hidden;
        //     .color-corner {
        //         transform:rotate(45deg);
        //         width: 50px;
        //         height: 75px;
        //         top: -30px;
        //         left: -20px;
        //         position: relative;
        //     }
        // }
    }

    /*
 END WALLPAPER PATTERN
 */

    /*
 START BLOG
 */
    .blog {
        display: grid;
        grid-template-columns: 1fr 40%;
        @include under-m {
            grid-template-columns: 1fr 35%;
        }
        @include under-s {
            grid-template-columns: 100%;
        }
    }
    /*
END BLOG
*/
    /*
    START MARKETING PANEL
    */
    .marketing-panel {
        img {
            max-width: 100%;
            width: 300px;
        }
    }
    /*
    END MARKETING PANEL
    */

    /*
 START FILTER SEARCH PAGE
 */
    .ais-results {
        display: grid;
        grid-gap: 120px 10px;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        grid-auto-rows: 210px;
        margin-top: 20px;
        margin-bottom: 120px;
        @include xs {
            grid-auto-rows: 240px;
        }
    }
    .input-group {
        display: flex;
        flex-direction: row;
        width: 100%;
        font-family: $content-font;
        border: $parent-border;
        color: $content-text-color;
        background-color: #fff;
        .search-input {
            width: 80%;
            height: 50px;
            border-left: none;
            border-top: none;
            border-bottom: none;      
        }
        .submit-search-button {
            font-family: $content-font;
        }
        button {
            cursor: pointer;
            width: 20%;
            background-color: #fff;  
            border: none;
            padding: 0;
            margin: 0;
            display: flex;
            justify-content: center;
            align-content: center;
            &:hover {
                background-color: $content-accent-color;
            }
        }
        
    }
    .ais-price-range {
        input.ais-price-range__input {
            font-family: $content-font;
            padding-left: 2%;
            border: $parent-border;   
        }
    }
    .ais-refinement-list {
        .ais-refinement-list__item {
            .ais-refinement-list__label {
                .ais-refinement-list__checkbox {
                    background-color: #fff;
                    border-radius: 0;
                }
            }
        }
    }
    .patterns {
        display: grid;
        grid-gap: 45px 10px;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        grid-auto-rows: 200px;
    }
    .ais-index {
        width: 100%;
        display: flex;
        flex-direction: row;
        box-sizing: border-box;
        position: relative;
    }
    .filter-search {
        .filter-container {
            display: flex;
            flex-direction: row;
            box-sizing: border-box;
            position: relative;
            margin: $container-margin;
        }

        .filters {
            width: $filter-width;
            min-width: 300px;
            /*max-height: calc(100vh - #{$header-height} - #{$master-offset} * 2 - 5px);*/
            background-color: $content-background-color;
            color: $content-text-color;
            border: $parent-border;
            box-sizing: border-box;
            padding: 15px 1%;
            margin-right: $master-offset;
            @include under-s {
                display: none;
            }
        }
        .filters-mobile {
            display: none;
            position: fixed;
            width: 70%;
            height: 100%;
            box-sizing: border-box;
            padding: 2% 5%;
            z-index: 99;
            background-color: $content-background-color;
            overflow: scroll;
            right: 0;
            top: 0;

            @include under-s {
                display: block;
            }
        }
        .close {
            cursor: pointer;
        }
        #mobile-filters-button {
            float: right;
            margin: 5px 0;
            @include over-s {
                display: none;
            }
        }
        .overlay {
            position: fixed;
            width: 100%;
            height: 100%;
            z-index: 99;
            top: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(0, 0, 0, 0.5);
        }
        .results {
            width: $results-width;
            background-color: $content-background-color;
            color: $content-text-color;
            border: $parent-border;
            padding: 0 1%;
            box-sizing: border-box;
            @include under-s {
                width: 100%;
            }
        }
    }
    .ais-sort-by-selector{
        font-family: $content-font;
        width: 100%;
        padding: 5px;
        margin: 10px 0;
        border: 1px solid $content-accent-color;
    }
    .ais-refinement-list__count{
       color: $red;
        text-decoration: underline;
        margin-left: 2.5px;
    }
    .ais-refinement-list__checkbox{
        border: 1px solid red;
    }
    .activeFilter {
        outline: $swatch-outline-width solid $highlight-color;
        outline-offset: $swatch-outline-offset;
    }

    .activePatternFilter:before {

        content: 'X ';
        color: $highlight-color;

    }

    .activeFilterText {
        font-weight: 800;
    }

    .color-filters {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .pattern-filters {

    }

    .pattern-filter-option {
        cursor: pointer;
    }

    $swatch-size: 25px;
    .color-swatch {
        box-sizing: border-box;
        height: $swatch-size;
        width: $swatch-size;
        margin: $swatch-margin;
        border: 1px solid $content-accent-color;
    }
    .filter-header{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    /*
 END FILTER SEARCH
 */
    /*
 START FAVORITES
 */
    .favorites {
        display: grid;
        grid-gap: 45px 20px;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        grid-auto-rows: 200px;
        padding-bottom: 30px;
    }

    .add-to-favorites {
        position: absolute;
        right: 2px;
        top: 0px;

        .heart {
            color: $red;
            cursor: pointer;
            font-size: 20px;
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: white;
            &:hover {
                color: darken($red, 10%);
            }
        }
        .added {
            color: black !important;
        }
    }

    /*
 END FAVORITES
 */
    /*
 START ARTISTS PAGE
 */
    .artists-page {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin: 5px 10vw;
        grid-column-gap: 20px;
        @include under-m {
            margin: 5px 5vw;
        }
        @include under-s {
            margin: $master-offset;
        }
        @include xs {
            grid-template-columns: 100%;
        }
        p {
            margin: 0;
        }
        .artists-container {
            .artist-list {
                width: 100%;
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-column-gap: 10px;
                .artist-content-block {
                    border: $parent-border;
                    margin-bottom: $master-offset;
                    padding: 10px 20px;
                    background-color: $content-background-color;
                    display: inline-block;
                    img {
                        max-width: 100%;
                    }
                    @include under-s {

                    }
                }
            }
        }
        .artist-info.container {
            margin: 0;
            @include xs {
                grid-row-start: 1;
            }
        }
        .marketing-panel {
            margin: 0;
        }
    }
    .artist-image{
        text-align: center;
        padding: 0 0 5px;
        img {
            padding: 5px;
            border: 2px solid $footer-cta-background-color;
        }
    }
    /*
 END ARTISTS PAGE
 */
    /*
 START ARTIST PAGE
 */
    .artist-page {
        img {
            max-width: 200px;
        }
    }

    /*
 END ARTIST PAGE
 */
    /*
 START EDUCATION PAGE
 */
    .education-page {
        .container {
            .content-block {
                display: grid;
                grid-template-columns: 1fr 30%;
                grid-template-rows: auto 1fr;
                grid-column-gap: 20px;
                @include under-m {
                    grid-template-columns: 1fr 25%;
                }
                @include under-s {
                    grid-template-columns: 1fr;
                    grid-template-rows: auto;
                    grid-row-gap: 20px;
                }
                .education-title {
                    grid-row: 1 / 2;
                    grid-column: 1 / -1;
                }
                .education-video-text {
                    grid-row: 2 / -1;
                    grid-column: 2 / -1;
                    @include under-s {
                        grid-column: 1 / -1;
                        grid-row: auto;
                    }
                    p {
                        margin-top: 0;
                    }
                }
                .education-video-wrap {
                    position: relative;
                    padding-bottom: 56.25%; /* 16:9 */
                    padding-top: 25px;
                    height: 0;
                    grid-row: 2 / -1;
                    grid-column: 1 / 2;
                    iframe {
                        border: none;
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }


        li {
            list-style-position: inside;
        }
    }

    /*
 END EDUCATION PAGE
 */
    /*
 START PDP PAGE
 */

    .pdp {
        .content-block {
            text-align: center;
        }
    }

    .pdp-wrap {
        display: inline-block;
        width: 100%;
        @include under-m {
            width: 100%;
        }
        .pdp-inner {
            text-align: center;
            display: flex;
            align-content: center;
            flex-direction: row;
            @include under-s {
                flex-direction: column;
            }
            .image-wrap {
                width: 55%;
                @include under-s {
                    width: 100%;
                }
            }
            .product-detail-wrap {
                margin-left: 1em;
                width: 45%;
                text-align: left;
                @include under-s {
                    margin-left: 0;
                    width: 80%;
                    align-self: center;
                }
                .add-to-cart {
                    .button {
                        width: 100%;
                        text-align: center;
                        padding-left: 0;
                        padding-right: 0;
                        max-width: 300px;
                        @include under-s {
                            max-width: 1000px;
                        }
                    }
                }
                .price {
                    font-size: 1.75em;
                    margin: 30px 0;
                }
                .material-picker {
                    display: flex;
                    justify-content: space-between;
                    width: 100%;
                    max-width: 300px;
                    @include under-s {
                        max-width: 1000px;
                    }
                    .material-option {
                        width: 100px;
                        padding: 10px 20px;
                        text-align: center;
                        cursor: pointer;
                        line-height: 1.25em;
                        &.selected {
                            border-color: $highlight-color;
                            background-color: $highlight-color;
                            color: #fff;
                        }
                        @include under-s {
                            width: 48%;
                            padding: 10px 0;
                        }
                    }
                }
                .color-picker {
                    margin: 20px 0;
                    @include under-s {
                        max-width: 1000px;
                    }
                    .color-option {
                        width: 75px;
                        height: 75px;
                        cursor: pointer;
                        border: none;
                        padding: 0;
                        border-radius: 0;
                        transition: border 0s;
                        -webkit-transition: border 0s;
                        margin-right: 10px;
                        box-sizing: border-box;
                        &:last-child {
                            margin-right: 0;
                        }
                        &.selected {
                            border: 5px solid #474747;
                        }
                        @include under-s {
                            width: 48%;
                            padding: 10px 0;
                            height: 50px;
                        }
                    }
                }
            }
            .pdp-image {
                width: 80%;
            }
        }
    }
    #room-view{
        //TODO : THIS NEEDS TO BE DYNAMICALLY UPDATED based on ROOM SIZE
        background-size:100px 100px;
        //TODO : if nonstandard size then this needs to updated
        // TODO : make sure updated data accounts for pattern size
        overflow: hidden;
    }
    #room-picker {
        .button {
            &.selected {
                border-color: $highlight-color;
                background-color: $highlight-color;
                color: #fff;
            }
        }
    }
    .room-view-image {
        max-width: 100%;
        margin-top: 50px;
    }

    .social-media-icon{
        color: $footer-cta-background-color;
        font-size: 1.5em;
        margin: 10px 2.5px;
        &:hover{
            color: darken($footer-cta-background-color, 10%);
        }
    }
    /*
 END PDP PAGE
 */
    /*
 START CART
 */
    .cart-page {
        .cart-container {
            display: flex;
            flex-direction: row;
            box-sizing: border-box;
            position: relative;
            @include under-s {
                flex-direction: column-reverse;
            }
        }
    }

    .cart-list {
        width: 70%;
        box-sizing: border-box;
        display: inline-block;
        @include under-m {
            width: 60%;
        }
        @include under-s {
            width: 100%;
        }
        .cart-list-inner {
            width: 90%;
            margin: auto;
        }
    }

    .cart-total {
        width: 30%;
        box-sizing: border-box;
        display: inline-block;
        margin-left: $master-offset;
        display: flex;
        flex-direction: column;
        @include under-m {
            width: 40%;
        }
        @include under-s {
            width: 100%;
            margin-left: 0;
        }
        .cart-line {
            display: flex;
            justify-content: space-between;
            padding: 20px 0;
        }
    }

    .cart-item {
        display: grid;
        grid-template-columns: 150px 1fr 50px;
        grid-template-rows: auto 1fr auto;
        margin-bottom: 50px;
        grid-column-gap: 20px;
        @include under-m {
            grid-template-rows: auto 1fr;
            grid-row-gap: 5px;
        }
        @include xs {
            grid-template-rows: auto auto auto auto auto;
            grid-template-columns: 1fr;
            grid-row-gap: 5px;
        }
        .cart-image-wrap {
            grid-area: 1 / 1 / -1 / 2;
            @include xs {
                grid-area: 1 / 1 / 2 / -1;
            }
            .cart-image {
                width: 150px;
                height: 150px;
                display: inline-block;
                @include xs {
                    width: 100%;
                    height: auto;
                }
            }
        }

        .pattern-name {
            grid-area: 1 / 2 / 2 / 3;
            @include xs {
                grid-area: 2 / 1 / 3 / -1;
            }
        }
        .subtext {
            grid-area: 2 / 2 / 3 / 3;
            @include under-m {
                display: none;
            }
        }
        .button-wrap {
            grid-area: 3 / 2 / -1 / 3;
            @include under-m {
                grid-area: 2 / 2 / 3 / 3;
                align-self: center;
            }
            @include xs {
                grid-area: 5 / 1 / 6 / -1;
            }
            .tiny.red.button {
                margin: 5px 0;
            }
        }
        .cart-item-price {
            grid-area: 1 / 3 / 2 / 4;
            @include under-m {
                grid-area: 2 / 3 / 3 / 4;
                align-self: center;
            }
            @include xs {
                grid-area: 4 / 1 / 5 / -1;
                font-size: 1.25em;
            }
        }
    }

    /*
 END CART
 */


    /*
 START QUANTITY SELECTOR
 */

.quantity-selector {
    display: flex;
    .minus-one, .plus-one {
        font-size: 1.25em;
        width: 40px;
        height: 36px;
        line-height: 36px;
        text-align: center;
        padding: 0 !important;
    }
    .quantity-selected {
        font-size: 1.25em;
        width: 40px;
        height: 36px;
        line-height: 36px;
        text-align: center;
        border: 1px solid $content-text-color;
        box-sizing: border-box;
        border-radius: 1%;
    }
}
    /*
 END QUANTITY SELECTOR
 */
    /*
 START
 */

    /*
 END
 */
    /*
 START
 */

    /*
 END
 */
    /*
 START
 */

    /*
 END
 */
    /*
 START
 */

    /*
 END
 */
