<template>
  <div class="artist-info container">
    <div class="content-block" v-if="$route.params.artist">
      <div class="artist-image">
      <img  :src="`${selectedArtist.data.artist_picture.url}`" :alt="`${selectedArtist.data.artist_name[0].text}`">
      </div>
      <div class="title center">{{selectedArtist.data.artist_name[0].text}}</div>
      <p v-for="line in selectedArtist.data.artist_bio">
          {{line.text}}
      </p>
    </div>
    <div v-else class="content-block">
      <h2>Select an artist to view more info!</h2>
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
    computed: {
      ...mapGetters({
        selectedArtist: 'getSelectedArtistFromStore'
      })
    },
    methods: {
      ...mapActions({
        getSelectedArtist: 'getSelectedArtist'
      })
    },
    created () {
        this.getSelectedArtist(this.$route.params.artist);        
    },
    updated () {
      this.getSelectedArtist(this.$route.params.artist);
    }
  }
</script>




