<template>
  <modal name="add-to-cart" adaptive=true width="700px" height="600px" class="add-to-cart modal">
      <div class="modal-wrap">
          <div class="modal-inner">
            <div class="title">{{product.design.name}} Added to Cart!</div>
            <div class="modal-info-wrap">
              <img :src="`${product.image}`" :alt="`${product.design.name}`">
              <div class="title">{{product.design.name}}</div>
              <quantity-selector></quantity-selector>
            </div>
          </div>
      </div>    
  </modal>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import QuantitySelector from './../pdp/QuantitySelector'

export default {
  components: {
    QuantitySelector
  },
  props: [
    'product'
  ],
  data() {
    return {

    }
  },
  methods: {
    hideModal() {
      this.$modal.hide('add-to-cart')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



