<template>
    <div class="ais-results">
        <div class="wallpaper-pattern">
            <a :href="'pdp/'+result.design.name">
                <img :src="result.image">
                <div class="result__info">
                    <h2 class="result__name">
                        <!-- <ais-highlight :result="result" attribute-name="design.name" /> -->
                        {{result.design.name}}
                    </h2>
                    <div class="result__type">
                        <!-- <ais-highlight :result="result" attribute-name="material" /> -->
                        {{result.material}}
                    </div>
                    <!--           <div class="result__rating">
                                    <template v-for="n in 5">
                                    <span v-if="n <= result.rating" class="result__star" :key="n"></span>
                                    <span v-else class="result__star&#45;&#45;empty" :key="n"></span>
                                    </template>
                                </div>-->
                    <div class="result__price">${{result.price}}</div>
                </div>
            </a>
        </div>
    </div>
</template>

<script>
import AddFavorite from '@/components/favorites/AddFavorite'

    export default {
        props: [
            'result'
        ],
        components : {
            AddFavorite
        }
    }
</script>



