<template>
  <div class="pattern-filters">
    <p v-for="patternType in patternTypes" v-on:click="togglePatternFilter(patternType)" v-bind:class="{activePatternFilter : patternType.active}" class="pattern-filter-option" :id="`pattern-filter-${patternType.name}`">
      {{patternType.name}}
    </p>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
    /*    components : {
     WallpaperPattern
     },*/
    computed: {
      ...mapGetters({
        patternTypes: 'getAlgoliaPatternsFromStore'
      })
    },
    methods: {
      ...mapActions({
        togglePatternFilter: 'togglePatternFilter'
      })
    },
    mounted () {
    }
  }
</script>



