<template>
    <div class="quantity-selector">
      <span class="minus-one button" @click="minusQuantity(product)">-</span><span class="quantity-selected">{{product.quantity}}</span><span class="plus-one button" @click="addQuantity(product)">+</span>
    </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
    props: [
      'product'
    ],
    computed: {
      ...mapGetters({
        })
    },
    methods: {
        ...mapActions({
            addQuantity: 'addQuantity',
            minusQuantity: 'minusQuantity'
        })
    }
  }
</script>



