<template>
  <div class="color-filters">
    <div v-for="color in colors" v-on:click="toggleColorFilter(color)" :id="`color-filter-${color.name}`">
      <div class="color-swatch" v-bind:style="{ 'background-color': color.name }" v-bind:class="{activeFilter : color.active}"></div>
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'

  export default {
/*    components : {
      WallpaperPattern
    },*/
    computed: {
      ...mapGetters({
        colors: 'getAlgoliaColorsFromStore'
      })
    },
    methods: {
      ...mapActions({
        toggleColorFilter: 'toggleColorFilter'
      })
    }
  }
</script>



