<template>
  <div class="add-to-cart">
    <div v-on:click="addToCartHere(pattern);showModal()" class="big button" :id="`add-to-cart-${pattern.sku}`">
      ADD TO CART
    </div>
    <add-to-cart-modal :product="pattern"></add-to-cart-modal>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import AddToCartModal from './../modals/AddToCartModal'

  export default {
    components: {
      AddToCartModal
    },
    props: [
        'pattern'
    ],
    methods: {
      ...mapActions({
        addToCartHere: 'addToCart',
      }),
      showModal() {
        this.$modal.show('add-to-cart')
      },
      hideModal() {
        this.$modal.hide('add-to-cart')
      }
    }
  }
</script>



